:root {
	--color-primary: #ffc80b;
	--color-gradient-start: #ffc80b;
	--color-gradient-stop: #ffc80b;
}

body {
	font-family: 'Roboto', sans-serif;
	font-size: 15px;
	line-height: 1.8;
	font-weight: 400;
	color: #666
}

.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6, h6 {
	font-family: 'Roboto', sans-serif;
	color: #000;
}

.h1, h1 {
	font-size: 52px;
	font-weight: 400;
	line-height: 1.4em;
}

.h2, h2 {
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4em;
}

.h3, h3 {
	font-size: 32px;
	font-weight: 500;
	line-height: 1.3em;
}

.h4, h4 {
	font-size: 25px;
	font-weight: 500;
	line-height: 1.5em;
}

.h5, h5 {
	font-size: 21px;
	font-weight: 500;
	line-height: 1.7em;
}

.h6, h6 {
	font-size: 18px;
	line-height: 1.7em;
	font-weight: 500;
	color: #0a0a0a;
}

.main-header {
	border-bottom: 1px solid rgba(#fff, 0.2);
}

.mainbar-wrap {
	padding-left: 35px;
	padding-right: 35px;
}

.navbar-brand {
	padding-top: 25px;
	padding-bottom: 25px;
}

.main-nav {
	
	> li {
		
		> a {
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 0.02em;
			color: #fff;
		}
		&.is-active > a,
		&.current-menu-item > a,
		&:hover > a,
		> a:hover {
			color: #fff;
		}
	}
}

.contact-form select,
.contact-form input,
.contact-form textarea,
.contact-form .ui-selectmenu-button {
	color: #000;
}

.ld-pf-item .ld-pf-bg {
	background: rgba(255, 200, 11, 0.9);
}

.main-footer {
	color: #b5b7c4;

	a:not(.btn) {
		color: #b5b7c4;

		&:hover {
			color: #fff;
		}
	}
}